// 

import request from './ajax';
import mockAjax from './mockAjax';

//  api/product/getBaseCategoryList  GET 

export const reqCategoryList=()=>{
    return request({
        url:'/product/getBaseCategoryList',
        method:"get"
    })
}

// mock

export const reqBannerList=()=>{
    return mockAjax({
        url:'/banner',
        method:'get'
    })
};

export const reqFloorList=()=>{
    return mockAjax({
        url:'/floor',
        method:'get'
    })
};

