export const reqSearchInfo=(searchParams)=>{
    return request({
        url:'/list',
        method:'post',
        data:searchParams
    })
};

// detail
// api/item/{ skuId } get
export const reqDetailInfo=(skuId)=>{
    return request({
        url:`/item/${skuId}`,
        method:'get'
    })
};

//  
// /api/cart/addToCart/{ skuId }/{ skuNum } post
export const reqShopCart=(skuId, skuNum)=>{
    return request({
        url:`/cart/addToCart/${ skuId }/${ skuNum }`,
        method:'post'
    })
}

